<template>
  <div>
    <!-- header begin -->
    <!-- <div class="nav-bar">
      <div class="nav-bar-left" @click="goBack()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">询价列表</div>
      <div class="nav-bar-right" @click="showPopup()">
        <i class="iconfont iconsousuo" />
      </div>
    </div> -->
    <!-- header end -->
    <headerBar :header-data="headerData" @searchClick="searchAction()" />
    <div v-if="pageStyles===0" class="container">
      <common-list
        ref="ggcommonlist"
        class="common-list"
        :post-url="list.postUrl"
        :req-data="list.reqData"
        :data-name="list.dataName"
        @returnData="getPrizeLog($event)"
      >
        <div class="content">
          <div v-for="(item,index) of jjShowList" :key="index" class="common-main box">
            <div class="list">
              <h3><div class="tit">{{ item.xmmc }}</div><div class="label">{{ item.xmzt }}</div></h3>
              <div class="info">
                <span>
                  <i class="iconfont iconbianma" />
                  {{ item.zbbh }}
                </span>
                <span v-if="item.cjsj">
                  <i class="iconfont iconshijian" />
                  {{ item.cjsj?getTime(item.cjsj):'' }}
                </span>
              </div>
              <div class="list-btn">
                <ul v-if="item.xmzt==='初始化'">
                  <li><van-button type="default" plain size="small" @click="goXjMemberView(item.id)"><i class="iconfont iconxiangqing" /> 查看</van-button></li>
                  <li><van-button type="default" plain size="small" @click="goXjMemberEdit(item.id)"><i class="iconfont iconxiugai" /> 修改</van-button></li>
                </ul>
                <ul v-else>
                  <li><van-button type="default" plain size="small" @click="goXjMemberView(item.id)"><i class="iconfont iconxiangqing" /> 查看</van-button></li>
                </ul>
                <div v-if="item.xmzt!=='已撤销'" class="list-btn-more" @click="showGdczPopup(item.xmzt,item.id)">
                  <i class="iconfont iconshenglvehao" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </common-list>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
    <searchMeun @goSearchAction="onSubmit($event)" @reSet="reSet">
      <van-field v-model="search.xmbh" label="项目编号" placeholder="请输入项目编号" input-align="right" />
      <van-field v-model="search.xmmc" label="项目名称" placeholder="请输入项目名称" input-align="right" />
      <van-cell v-model="xmztShow" title="项目状态" value="请选择项目名称" is-link @click="showXmztPopup" />
      <van-cell v-model="fbsjShow" title="发布时间" is-link @click="showcalendar" />
    </searchMeun>
    <!-- 查询条件  begin -->
    <!-- <van-popup v-model="show" position="top" class="search-popup" overlay-class="transparent-overlay">
      <div class="common-main box">
        <div class="spe-input">
          <van-field v-model="search.xmbh" label="项目编号" placeholder="请输入项目编号" input-align="right" />
          <van-field v-model="search.xmmc" label="项目名称" placeholder="请输入项目名称" input-align="right" />
          <van-cell v-model="search.xmzt" title="项目状态" is-link @click="showXmztPopup" />
          <van-cell v-model="search.fbsj" title="发布时间" is-link @click="showcalendar" />
        </div>
        <div class="search-popup-btn">
          <van-button type="info" block round @click="searchReset()">重置</van-button>
          <van-button type="info" block round @click="onSubmit()">搜索</van-button>
        </div>
      </div>
    </van-popup> -->
    <!-- 查询条件  end -->

    <searchPick
      :pick-title="searchPick.PickTitle"
      :pick-columns="searchPick.PickColumns"
      @confirmClick="confirmPick($event)"
    />

    <!-- 日期选择 begin -->
    <van-calendar v-model="show2" type="range" color="#1890FF" :min-date="mindate" @confirm="onConfirm" />
    <!-- 日期选择 begin -->

    <!-- 更多操作弹出层 begin -->
    <van-action-sheet v-model="show3" title="更多操作" cancel-text="取消">
      <div class="action-button">
        <ul>
          <!-- <li @click="goBidMemberHistory()"><i class="iconfont iconlishijilu" />竞价历史</li> -->
          <li v-if="xmzt==='询价结束' || xmzt==='已定标' || xmzt==='已结果公告'" @click="getResult()"><i class="iconfont iconyiyuyue" />询价结果</li>
          <li v-if="xmzt==='询价中'" @click="goJsxj()"><i class="iconfont iconicon-jieshu" />结束询价</li>
          <li v-if="xmzt==='初始化'" @click="goXjTj()"><i class="iconfont iconhegezhihuizonglei" />提交</li>
          <li v-if="xmzt==='询价中' || xmzt==='询价结束'" @click="goXjback()"><i class="iconfont iconchexiao" />撤销</li>
          <li v-if="xmzt==='初始化'" @click="goXjdelete()"><i class="iconfont iconshanchu" />删除</li>
        </ul>
      </div>
    </van-action-sheet>
    <!-- 更多操作弹出层 end -->
  </div>
</template>

<script land="script" scoped>
import errorPage from '@/components/result-page/index'
import { dsxjList, createDsxjXmxx, deleteDsxjXmxx, execRevokeDsxjXmxx, execStopDsxj } from '@/store/module.js'
import commonList from '@/components/list/commonList1.vue'
import searchPick from '@/components/searchComponents/searchPick.vue'
import searchMeun from '@/components/searchComponents/searchMeun.vue'
import headerBar from '@/components/header/headerBar.vue'
export default {
  name: 'XjMemberList',
  components: {
    errorPage,
    searchMeun,
    headerBar,
    commonList,
    searchPick
  },
  data() {
    return {
      xmztShow: '请选择项目状态',
      searchPick: {
        PickTitle: '',
        PickColumns: [{
          label: '请选择项目状态',
          value: ''
        }, {
          label: '初始化',
          value: 0
        }, {
          label: '询价中',
          value: 20
        }, {
          label: '询价结束',
          value: 40
        }, {
          label: '已结果公告',
          value: 55
        }, {
          label: '已撤销',
          value: -1
        }
        ]
      },
      fbsjShow: '请选择发布时间',
      headerData: {
        title: '询价列表',
        leftUrl: 'back',
        leftText: ' ',
        showSearch: true
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      pageStyles: 0,
      mindate: new Date(2010, 0, 1),
      jjShowList: [], // 竞价展示列表
      temp: [],
      search: {
        xmbh: '',
        xmmc: '',
        xmzt: '',
        fbsj: ''
      },
      xmid: '',
      xmzt: '',
      tableData: [], // 所有竞价项目
      date: '',
      value: '',
      value1: '',
      show: false,
      show1: false,
      show2: false,
      show3: false,
      columns: ['请选择项目状态', '初始化', '询价中', '询价结束', '已结果公告', '已撤销']
    }
  },
  computed: {

  },
  created() {
    this.init()
  },

  methods: {
    confirmPick(pick) {
      this.xmztShow = pick.label
      this.search.xmzt = pick.value
    },
    getPrizeLog(data) {
      this.jjShowList = data
    },
    reSet() {
      this.search.xmbh = ''
      this.search.xmmc = ''
      this.search.xmzt = ''
      this.search.fbsj = ''
      this.xmztShow = '请选择项目状态'
      this.fbsjShow = '请选择发布时间'
    },
    init() {
      var kssj = ''
      var jssj = ''
      if (this.search.fbsj) {
        kssj = this.zhTime1(this.search.fbsj[0])
        jssj = this.zhTime2(this.search.fbsj[1])
      }
      this.list.postUrl = dsxjList
      this.list.reqData = {
        ryid: this.$store.state.mh.mhUserInfo.id,
        xmmc: this.search.xmmc,
        zbbh: this.search.xmbh,
        // xmzt: this.search.xmzt,
        kssj: kssj,
        jssj: jssj,
        pageno: 1,
        pagerow: 10
      }
      this.list.dataName = 'list'
      this.list.noDataText = '暂无更多订单'
    },
    parentReceive() {
      this.onSubmit()
    },
    onClick(val) {
      this.search.xmzt = val
      this.show1 = false
    },
    getTime(item) {
      item = item + ''
      return item.slice(0, 4) + '-' + item.slice(4, 6) + '-' + item.slice(6, 8) + ' ' + item.slice(8, 10) + ':' + item.slice(10, 12)
    },
    onSubmit() {
      this.$bus.$emit('showMeun', false)
      var kssj = ''
      var jssj = ''
      if (this.search.fbsj) {
        kssj = this.zhTime1(this.search.fbsj[0])
        jssj = this.zhTime2(this.search.fbsj[1])
      }
      const reqData = {
        ryid: this.$store.state.mh.mhUserInfo.id,
        xmmc: this.search.xmmc,
        zbbh: this.search.xmbh,
        // xmzt: this.search.xmzt,
        kssj: kssj,
        jssj: jssj,
        pageno: 1,
        pagerow: 10
      }
      this.$bus.$emit('refList1', reqData)
      this.show = false
    },
    zhTime1(time) {
      const year = time.getFullYear()
      const month = (time.getMonth() + 1) < 10 ? ('0' + (time.getMonth() + 1)) : (time.getMonth() + 1)
      const day = time.getDate() < 10 ? ('0' + time.getDate()) : time.getDate()
      const hour = '00'
      const minute = '00'
      const sj = year + '' + month + '' + day + '' + hour + '' + minute
      return sj
    },
    zhTime2(time) {
      const year = time.getFullYear()
      const month = (time.getMonth() + 1) < 10 ? ('0' + (time.getMonth() + 1)) : (time.getMonth() + 1)
      const day = time.getDate() < 10 ? ('0' + time.getDate()) : time.getDate()
      const hour = '23'
      const minute = '60'
      const sj = year + '' + month + '' + day + '' + hour + '' + minute
      return sj
    },
    goJsxj() {
      execStopDsxj.data = { xmxh: this.xmid }
      this.asyncApiModule(execStopDsxj).then(res => {
        this.$toast.success('结束询价成功')
        this.show3 = false
        this.$bus.$emit('refList1')
      }).catch(error => {
        console.log(error)
      })
    },
    goXjMemberView(id) {
      this.$store.commit('bid/SET_XMXH', id)
      this.$router.push('XjMemberView')
    },
    getResult() {
      this.$store.commit('bid/SET_XMXH', this.xmid)
      this.$store.commit('bid/SET_XMZT', this.xmzt)
      this.$router.push({
        name: 'XjMemberResult'
      })
    },
    goXjback() {
      execRevokeDsxjXmxx.data = { xmid: this.xmid }
      this.asyncApiModule(execRevokeDsxjXmxx).then(res => {
        this.$toast.success('撤销成功')
        this.show3 = false
        this.$bus.$emit('refList1')
      }).catch(error => {
        console.log(error)
      })
    },
    goXjdelete() {
      deleteDsxjXmxx.data = { xmid: this.xmid }
      this.asyncApiModule(deleteDsxjXmxx).then(res => {
        this.$toast.success('删除成功')
        this.show3 = false
        this.$bus.$emit('refList1')
      }).catch(error => {
        console.log(error)
      })
    },
    goXjTj(xmid) {
      createDsxjXmxx.data = { xmid: this.xmid }
      this.asyncApiModule(createDsxjXmxx).then(res => {
        this.$toast.success('提交成功')
        this.show3 = false
        this.$bus.$emit('refList1')
      }).catch(error => {
        console.log(error)
      })
    },

    goBack() {
      this.$router.back()
    },
    showPopup() {
      this.show = true
    },
    showXmztPopup() {
      this.$bus.$emit('showPick', true)
      this.searchPick.PickTitle = '选择项目状态'
    },
    showcalendar() {
      this.show2 = true
    },
    showGdczPopup(xmzt, xmid) {
      this.xmzt = xmzt
      this.xmid = xmid
      this.show3 = true
    },
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    onConfirm(date) {
      this.search.fbsj = date
      const [start, end] = date
      this.show2 = false
      this.fbsjShow = `${this.formatDate(start)} - ${this.formatDate(end)}`
    },
    goXjMemberEdit(id) {
      this.$router.push({
        name: 'XjMemberEdit',
        params: {
          xmxh: id
        }
      })
    }
  }

}
</script>

<style lang="less" scoped>

</style>
